.body {
    flex: 0.8;
    background: linear-gradient(rgb(91, 87, 115), rgba(0, 0, 0, 1));
    color: white;
    padding: 30px;
    height: 100vh;
    color: white;
    overflow-y:overlay;
    padding: 30px;
}
.body::-webkit-scrollbar{
    display: none;
}

.body__info{
    display: flex;
    align-items: flex-end;
    padding: 10px;
}

.body__infoText{
    flex: 1;
}

.body__infoText >h2{
    font-size: 48px;
    margin-bottom: 10px;
}

.body__info >img{
    height: 20vw;
    margin: 0 20px;
    box-shadow:0 4px 60px rgba(0, 0, 0, 0.5) ;
}
.body__shuffle{
    font-size: 60px !important;
    margin-left: 50px;
    margin-bottom: -18px;
    padding: 10px;
}

.body__shuffle:hover{
    transition: 100ms transform ease-in;
    transform: scale(1.08);
}

.body__songs{
    margin: 20px -30px;
}

.body__infoText >p{
    font-size: 14px;
}