.sidebarOption{
    display: flex;
    align-items: center;
    color: grey; 
    height: 40px;
    transition: 200ms color ease-in;
    cursor: pointer;
}
.sidebar >hr{
    border:1px solid #282828;
    width: 90%;
    margin: 10px auto;
}

.sidebarOption:hover{
    color: white;
}

.sidebarOption__icon{
    padding-left: 2 0px;
    padding-right: 10px;
}

.sidebar__title{
    margin-left: 10px;
    padding: 5px;
    font-size: 12px;
}

.sidebarOption >p{
    margin-top: 10px;
    margin-left: 20px;
    font-size: 14px;
}